body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-linear-gradient(left, #B2DFDB, #fff);
  background-image: linear-gradient(to right, #B2DFDB, #fff);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.main-container {
  height: auto;
  width: 100%;
}

.section-heading {
  margin: 0.5em 0em;
  padding: 0.25em 0.5em;
}

@media screen and (min-width: 1000px){
  .main-container {
    height: 100vh;
    margin-bottom: 1em;
  }
  .preview-container {
    position: absolute;
    overflow: scroll;
  }
  .editor-container {
    height: 100%;
  }

  .section-heading {
    margin-top: 0;
  }

}
